body {
  /* margin: 0;
  padding: 0; */
  font-family: 'Courier New', Courier, monospace;
  background: rgba(10, 161, 116, 0.89);
  position: relative;
}

#root {
  padding-top: 10px;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 15px;
  margin-top: 30px;  
  border-radius: 10px;
  background-color: rgb(236, 235, 235);
}

/* input:focus {
  outline: none !important;
} */
.titulo{    
    text-align: center;    
    font-size: 24px;
}
.inputsearch {
    border: 0px;
    border-radius: 25px;
    height: 40px;
    width: 92%;
    margin: auto;
    font-weight: 400;
    justify-content: center;
    margin-bottom: 20px;
    box-shadow: inset -2px -5px 18px -4px rgba(0,0,0,0.75);
    padding: 3px 15px 3px 15px;
    color: black;
}

.inputsearch::-webkit-input-placeholder {
    padding-left: 5px;
    color: black;
}

.inputsearch::placeholder {
    padding-left: 5px;
    color: black;
}

.inputsearch:focus {
    border: 1px rgba(255, 255, 255, 0.788) solid;
}
.lista {
    list-style: none;
    padding: 0px;
}
.item {
    display: flex;
    justify-content: left;
    text-align: right;
    align-items: center;
    /* box-shadow: 10px 11px 16px 4px rgba(0, 0, 0, 0.7); */
    box-shadow: 12px 14px 8px 2px rgba(0, 0, 0, 0.75);
    margin-bottom: 50px;
    padding: 5px 15px 5px 5px;
    border: 0px;
    border-radius: 10px 10px 10px 10px;
    background-color: #1c59a8;
}

.item-p {
    color: white;
    font-weight: 700;
    cursor: pointer;
}

.item-p-complete {
    text-decoration: line-through;
}

.delete {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 25px;
    font-weight: 400;
    width: 25px;
    border-radius: 25px;
    height: 25px;
    padding: 10px;
    margin-left: 15px;
    cursor: pointer;
    background-color: white;
    text-align: center;
    align-items: center;
    position: absolute;
    right: 60px;
}

.delete:hover {
    background-color: rgb(218, 218, 218);
    color: red;
}

.icon {
    float: right;
    font-size: 30px;
    font-weight: 400;
    font-family: Arial, Helvetica, sans-serif;
    cursor: pointer;
}

.icon-check {
    color: white;
    margin-right: 10px;
}

.icon-check:hover {
    color: chartreuse;
}

.icon-check-active {
    color: chartreuse;
}
.boton {
    position: static;
    float: right;
    width: 64px;
    height: 64px;
    font-weight: 400;
    font-size: 40px;
    border-radius: 40px;
    border: 0px;
    bottom: 365px;
    z-index: 2;
    background-color: #1c59a8;
    color: white;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    transition: .3s ease;
}

.boton:hover {
    background-color: #1c59a8d0;
    -webkit-transform: rotate(224deg);
            transform: rotate(224deg);
}

.box-boton {
    justify-content: flex-end;
    display: flex;
}
.load-container {
    position: relative;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    margin-bottom: 20px;
    background: white;
    border: 1px solid #e5e6e9;
    border-radius: 5px;
    /* max-width: 769px; */
    padding: 1rem;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.load-background {
    /* animation-name: skeletonloaging;
    animation-duration: 2s;
    animation-timing-function: linear;
    animation-iteration-count: infinite; */
    -webkit-animation: skeletonloaging 2s linear infinite alternate;
            animation: skeletonloaging 2s linear infinite alternate;
    background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
    min-height: 100px;
}

@-webkit-keyframes skeletonloaging {
    0% {        
        background-color: hsl(200, 20%, 70%);
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {        
        background-color: hsl(200, 20%, 70%);
        background-position: 0% 50%;
    }
}

@keyframes skeletonloaging {
    0% {        
        background-color: hsl(200, 20%, 70%);
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {        
        background-color: hsl(200, 20%, 70%);
        background-position: 0% 50%;
    }
}

.bar {
    height: 45px;
    position: relative;
    width: 100%;
    border-radius: 10px;
}

.mask {
    background: white;
    width: 100%;
}

.thick {
    height: 1.5rem;
}

.thin {
    height: 0.5rem;
}

/* Sin datos */
.sindata-container {
    justify-content: center;
    align-items: center;
    text-align: center;
}
form {
    display: flex;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    margin-left: 5%;
    margin-right: 5%;
    padding-left: 5%;
    padding-right: 5%;
    background-color: #fff;
    color: black;
    border-radius: 15px;
}

label {
    margin-top: 30px;
    margin-bottom: 20px;
    font-size: 24px;
}

textarea {
    border-radius: 15px 15px 1px 15px;
    box-shadow: inset -2px -5px 18px -4px rgba(0, 0, 0, 0.75);
    padding: 10px 15px 3px 15px;
    margin-bottom: 20px;
    width: calc(100% - 35px) !important;
}

.todoform-button {
    border: 0px;
    border-radius: 10px;
    box-shadow: 10px 11px 16px 4px rgba(0, 0, 0, 0.7);
    margin-bottom: 50px;
    height: 60px;
    width: 100px;
    text-transform: uppercase;
    font-weight: 600;
}

.todoform-button-cancel {
    margin-right: 30px;
}

.todoform-button-cancel:hover {
    background-color: rgb(218, 218, 218);
}

.todoform-button-add {
    background-color: #1c59a8;
    margin-left: 30px;
    color: white;
}

.todoform-button-add:hover {
    background-color: #1c59a8b7;
}
.modal-container {
    position: fixed;
    background: rgba(32, 35, 41, .8);
    color: white;
    justify-content: center;
    align-items: stretch;
    text-align: center;    
    top: -10px;
    left: 0px;
    right: 0px;
    bottom: -10px;
    z-index: 1;
    width: 100%;
}

.modal-body {
    position: relative;
    margin: auto;
    top: 30%;
}
